import { DataGrid } from "@material-ui/data-grid";
import "./animalList.css";
import { Animais } from "./../../data";
import { Link } from "react-router-dom";
import { Delete, Edit } from "@material-ui/icons";
const columns = [
  { field: "id", headerName: "Cod", width: 130 },
  {
    field: "nome",
    headerName: "Nome",
    width: 150,
  },
  {
    field: "dataNascimento",
    headerName: "Data Nasc.",
    width: 150,
  },
  {
    field: "dono",
    headerName: "Dono",
    width: 150,
  },
  {
    field: "tipo",
    headerName: "Tipo",
    width: 150,
  },
  {
    field: "raca",
    headerName: "Raça",
    width: 150,
  },
  {
    field: "acoes",
    headerName: "Ações",
    width: 150,
    renderCell: (params) => {
      return (
        <div>
          <Delete className="tableIconRemove" />
          <Edit className="tableIconEdit" />
        </div>
      );
    },
  },
];

export default function AnimalList() {
  return (
    <div className="animalList">
      <div className="animalListTopWrapper">
        <h3 className="animalListTitle">Animais</h3>
        <Link to="/novoanimal">
          <button className="animalListNewVetBtn">Novo</button>
        </Link>
      </div>
      <DataGrid
        rows={Animais}
        columns={columns}
        pageSize={5}
        checkboxSelection
        disableSelectionOnClick
      />
    </div>
  );
}
