import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import "./App.css";
import LeftMenu from "./components/leftMenu/LeftMenu";
import NavBar from "./components/navbar/NavBar";
import Animais from "./pages/animais/Animais";
import AnimalList from "./pages/animalList/AnimalList";
import Consulta from "./pages/consultas/Consultas";
import Consultas from "./pages/consultas/Consultas";
import Home from "./pages/home/Home";
import NewAnimal from "./pages/newAnimal/NewAnimal";
import NewVet from "./pages/newVet/NewVet";
import NovaConsulta from "./pages/novaConsulta/NovaConsulta";
import Veterinarios from "./pages/veterinarios/Veterinarios";
import VetList from "./pages/vetList/VetList";

function App() {
  return (
    <div className="App">
      <NavBar />
      <Router>
        <div className="container">
          <LeftMenu />
          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route path="/animais">
              <AnimalList />
            </Route>
            <Route path="/veterinarios">
              <VetList />
            </Route>
            <Route path="/consultas">
              <Consultas />
            </Route>

            <Route path="/novoAnimal">
              <NewAnimal />
            </Route>
            <Route path="/novoVeterinario">
              <NewVet />
            </Route>
            <Route path="/consultas">
              <Consulta />
            </Route>
            <Route path="/novaconsulta">
              <NovaConsulta />
            </Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;
