import "./leftMenu.css";
import { Face, Home, Pets, Event } from "@material-ui/icons";
import { Link } from "react-router-dom";
export default function LeftMenu() {
  return (
    <div className="leftMenu">
      <ul className="leftMenuButtons">
        <Link to="/">
          <li className="leftMenuItem">
            <Home className="leftMenuIcon" />
            Inicio
          </li>
        </Link>
        <Link to="/animais">
          <li className="leftMenuItem">
            <Pets className="leftMenuIcon" />
            Animais
          </li>
        </Link>
        <Link to="/veterinarios">
          <li className="leftMenuItem">
            <Face className="leftMenuIcon" />
            Veterinários
          </li>
        </Link>
        <Link to="/consultas">
          <li className="leftMenuItem">
            <Event className="leftMenuIcon" />
            Consultas
          </li>
        </Link>
      </ul>
    </div>
  );
}
