import "./newAnimal.css";

export default function NewAnimal() {
  return (
    <div className="newAnimal">
      <div className="topWrapper">
        <h3 className="topWrapperTitle">Novo animal</h3>
      </div>
      <form className="newAnimalForm">
        <div className="newAnimalFormItem">
          <label htmlFor="newAnimalName" className="newAnimalFormLabel">
            Nome
          </label>
          <input type="text" className="newAnimalInput" />
        </div>
        <div className="newAnimalFormItem">
          <label htmlFor="newAnimalName" className="newAnimalFormLabel">
            Data Nascimento
          </label>
          <input type="text" className="newAnimalInput" />
        </div>
        <div className="newAnimalFormItem">
          <label htmlFor="newAnimalName" className="newAnimalFormLabel">
            Tipo
          </label>
          <input type="text" className="newAnimalInput" />
        </div>
        <div className="newAnimalFormItem">
          <label htmlFor="newAnimalName" className="newAnimalFormLabel">
            Raça
          </label>
          <input type="text" className="newAnimalInput" />
        </div>
        <div className="newAnimalFormItem">
          <label htmlFor="newAnimalName" className="newAnimalFormLabel">
            Dono
          </label>
          <input type="text" className="newAnimalInput" />
        </div>
        <div className="newAnimalFormBtnGroup">
          <button type="text" className="newAnimalBtn btnReset">
            Resetar
          </button>
          <button type="text" className="newAnimalBtn btnCadastrar">
            Cadastrar
          </button>
        </div>
      </form>
    </div>
  );
}
