import "./consultas.css";
import * as Data from "./../../data";
import { DataGrid } from "@material-ui/data-grid";
import { Delete, Edit } from "@material-ui/icons";
import { Link } from "react-router-dom";
const columns = [
  { field: "id", headerName: "Cod", width: 130 },
  {
    field: "animalInfo",
    headerName: "Info. Animal Paciente",
    width: 300,
  },
  {
    field: "dataEHora",
    headerName: "Data e Hora",
    width: 250,
  },
  {
    field: "nomeVeterinario",
    headerName: "Veterinário",
    width: 150,
  },
  {
    field: "acoes",
    headerName: "Ações",
    width: 150,
    renderCell: (params) => {
      return (
        <div>
          <Delete className="tableIconRemove" />
          <Edit className="tableIconEdit" />
        </div>
      );
    },
  },
];

const rows = Data.Consultas.map((consulta) => {
  return {
    id: consulta.id,
    animalInfo: `${Data.Animais[consulta.idAnimal].nome} - ${
      Data.Animais[consulta.idAnimal].raca
    } - ${Data.Animais[consulta.idAnimal].tipo}`,
    dataEHora: consulta.dataEHora,
    nomeVeterinario: `${Data.Veterinarios[consulta.idVeterinario].nome}`,
  };
});
export default function Consultas() {
  return (
    <div className="consultas">
      <div className="topWrapperConsultas">
        <h3 className="consultarTitle">Consultas agendadas</h3>
        <Link to="novaConsulta">
          <button className="novaConsultaBtn">Agendar</button>
        </Link>
      </div>
      <DataGrid
        rows={rows}
        columns={columns}
        pageSize={5}
        checkboxSelection
        disableSelectionOnClick
      />
    </div>
  );
}
