import "./vetList.css";
import { DataGrid } from "@material-ui/data-grid";
import { Veterinarios } from "./../../data";
import { Delete, Edit } from "@material-ui/icons";
import { Link } from "react-router-dom";
const columns = [
  { field: "id", headerName: "Cod", width: 130 },
  {
    field: "nome",
    headerName: "Nome",
    width: 150,
  },
  {
    field: "dataNascimento",
    headerName: "Data Nasc.",
    width: 150,
  },
  {
    field: "cpf",
    headerName: "CPF",
    width: 150,
  },
  {
    field: "registro",
    headerName: "RG",
    width: 150,
  },
  {
    field: "acoes",
    headerName: "Ações",
    width: 150,
    renderCell: (params) => {
      return (
        <div>
          <Delete className="tableIconRemove" />
          <Edit className="tableIconEdit" />
        </div>
      );
    },
  },
];

export default function VetList() {
  return (
    <div className="vetList">
      <div className="vetListTopWrapper">
        <h3 className="vetListTitle">Veterinários</h3>
        <Link to="/novoVeterinario">
          <button className="vetListNewVetBtn">Novo</button>
        </Link>
      </div>
      <DataGrid
        rows={Veterinarios}
        columns={columns}
        pageSize={5}
        checkboxSelection
        disableSelectionOnClick
      />
    </div>
  );
}
