import "./newVet.css";
export default function NewVet() {
  return (
    <div className="newVet">
      <div className="topWrapper">
        <h3 className="topWrapperTitle">Novo veterinário</h3>
      </div>
      <form className="newVetForm">
        <div className="newVetFormItem">
          <label htmlFor="newVetName" className="newVetFormLabel">
            Nome
          </label>
          <input type="text" className="newVetInput" />
        </div>
        <div className="newVetFormItem">
          <label htmlFor="newVetName" className="newVetFormLabel">
            Data Nascimento
          </label>
          <input type="text" className="newVetInput" />
        </div>
        <div className="newVetFormItem">
          <label htmlFor="newVetName" className="newVetFormLabel">
            CPF
          </label>
          <input type="text" className="newVetInput" />
        </div>
        <div className="newVetFormItem">
          <label htmlFor="newVetName" className="newVetFormLabel">
            RG
          </label>
          <input type="text" className="newVetInput" />
        </div>
        <div className="newVetFormBtnGroup">
          <button type="text" className="newVetBtn btnReset">
            Resetar
          </button>
          <button type="text" className="newVetBtn btnCadastrar">
            Cadastrar
          </button>
        </div>
      </form>
    </div>
  );
}
