import "./navbar.css";
import { ExitToApp, SettingsApplications } from "@material-ui/icons";
export default function NavBar() {
  return (
    <div className="navBar">
      <div className="navBarWrapper">
        <div className="navBarLeftSide">
          <span className="logo">Vet Consultas</span>
        </div>
        <div className="navBarRightSide">
          <SettingsApplications className="navBarIcon" />
          <ExitToApp className="navBarIcon" />
        </div>
      </div>
    </div>
  );
}
