import "./novaConsulta.css";

export default function NovaConsulta() {
  return (
    <div className="novaConsulta">
      <div className="topWrapperNovaConsulta">
        <h3 className="topWrapperTitle">Nova Consulta</h3>
      </div>
      <form className="novaConsultaForm">
        <div className="novaConsultaFormItem">
          Selecione o animal
          <select name="animal" id="">
            <option value="">lassie</option>
            <option value="">spike</option>
            <option value="">minnie</option>
          </select>
        </div>
        <div className="novaConsultaFormItem">
          Selecione o veterinario
          <select name="animal" id="">
            <option value="">mauro</option>
            <option value="">susan</option>
            <option value="">marcio</option>
          </select>
        </div>
        <div className="novaConsultaFormItem">
          Selecione a data
          <input type="date" />
        </div>
        <div className="novaConsultaFormItem">
          Indique a hora
          <input type="time" />
        </div>
        <div className="novaConsultaFormItem">
          <button className="novaConsultaSalvarBtn">salvar</button>
        </div>
      </form>
    </div>
  );
}
